<template>
  <div class="header">
    <div class="logo fl">
      <img src="@a/logo.png" alt="" srcset="" />
      <span></span>
    </div>
    <div class="user fr">
      <img src="@a/logo.png" alt="" srcset="" />
      <p>
        {{ $store.state.userInfo.role_name }}<span>{{
          $store.state.userInfo.user_name || $store.state.userInfo.user_username
        }}</span
        ><el-button type="text" @click="open"></el-button>
      </p>
    </div>
  </div>
</template>

<script>
import { logout } from "@api/user.js";
export default {
  data() {
    return {};
  },
  methods: {
    open() {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(async () => {
          const result = await logout();
          if (result) {
            this.$message({
              type: "success",
              message: "!"
            });
            this.$store.dispatch("login", {});
            this.$router.replace("/login");
          }
        })
        .catch();
    }
  }
};
</script>

<style lang="scss" scoped>
@import "./header.scss";
</style>
