<template>
  <div class="user-add">
    <el-tabs v-model="activeName">
      <el-tab-pane label="" name="first">
        <CourseForm></CourseForm>
      </el-tab-pane>
      <el-tab-pane label="" name="second">
        <div class="user-add-header cl">
          <el-upload
            :disabled="false"
            action="customize"
            name="openFile"
            :http-request="uploadFile"
            :file-list="file"
            :show-file-list="false"
            class="fr"
          >
            <el-button type="primary" class="fr" icon="el-icon-bottom-right"
              ></el-button
            >
          </el-upload>
          <el-button type="text" class="fr"></el-button>
        </div>
        <div class="user-add-content">
          <div>
            <el-button type="text" class="fl all" @click="allDelete"
              ></el-button
            >
          </div>
          <el-table
            ref="multipleTable"
            :data="courseList"
            tooltip-effect="dark"
            style="width: 100%"
            @selection-change="handleSelectionChange"
          >
            <el-table-column type="selection" width="50"> </el-table-column>
            <el-table-column
              label=""
              width="50"
              type="index"
            ></el-table-column>
            <el-table-column label="" min-width="170" align="center">
              <template slot-scope="scope"
                ><el-input v-model="scope.row.course_name"></el-input
              ></template>
            </el-table-column>
            <el-table-column label="" min-width="170" align="center">
              <template slot-scope="scope">
                <el-input v-model="scope.row.course_hour"></el-input>
              </template>
            </el-table-column>
            <el-table-column label="" min-width="170" align="center">
              <template slot-scope="scope">
                <el-input v-model="scope.row.course_classroom"></el-input>
              </template>
            </el-table-column>
            <el-table-column label="" min-width="170" align="center">
              <template slot-scope="scope">
                <el-input v-model="scope.row.course_credit"></el-input>
              </template>
            </el-table-column>
            <el-table-column
              label=""
              min-width="270"
              align="center"
              class="year-ipt"
            >
              <template slot-scope="scope">
                <el-col :span="11">
                  <el-date-picker
                    format="yyyy"
                    value-format="yyyy"
                    v-model="scope.row.course_year_begin"
                    type="year"
                    style="width:100%"
                  >
                  </el-date-picker>
                </el-col>
                <el-col :span="2">
                  <div style="text-align:center">-</div>
                </el-col>
                <el-col :span="11">
                  <el-date-picker
                    format="yyyy"
                    value-format="yyyy"
                    v-model="scope.row.course_year_end"
                    type="year"
                    style="width:100%"
                  >
                  </el-date-picker>
                </el-col>
              </template>
            </el-table-column>
            <el-table-column label="" min-width="170" align="center">
              <template slot-scope="scope">
                <el-input v-model="scope.row.course_amount"></el-input>
              </template>
            </el-table-column>
            <el-table-column label="" min-width="200" align="center">
              <template slot-scope="scope">
                <el-select
                  v-model="scope.row.course_type"
                  placeholder=""
                  style="width:100%"
                >
                  <el-option
                    label=""
                    value=""
                  ></el-option>
                  <el-option
                    label=""
                    value=""
                  ></el-option>
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                </el-select>
              </template>
            </el-table-column>
            <el-table-column label="" min-width="170" align="center">
              <template slot-scope="scope">
                <el-select
                  v-model="scope.row.course_assess"
                  placeholder=""
                  style="width:100%"
                >
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                </el-select>
              </template>
            </el-table-column>
            <el-table-column label="" min-width="170" align="center">
              <template slot-scope="scope">
                <el-select
                  v-model="scope.row.teacher_id"
                  placeholder=""
                  style="width:100%"
                >
                  <el-option
                    :label="item.user_name"
                    :value="item.teacher_id"
                    v-for="item in $store.state.userTeacherList"
                    :key="item.teacher_id"
                  ></el-option>
                </el-select>
              </template>
            </el-table-column>
            <el-table-column label="" min-width="170" align="center">
              <template slot-scope="scope">
                <el-input
                  type="textarea"
                  :rows="2"
                  placeholder=""
                  v-model="scope.row.course_info"
                >
                </el-input>
              </template>
            </el-table-column>
            <el-table-column label="" width="100" fixed="right">
              <template slot-scope="scope">
                <el-button
                  type="danger"
                  icon="el-icon-delete"
                  size="mini"
                  circle
                  @click="addOrRemove(scope, true)"
                  :disabled="courseList.length === 1"
                ></el-button>
                <el-button
                  type="primary"
                  icon="el-icon-plus"
                  size="mini"
                  circle
                  v-if="scope.$index === courseList.length - 1"
                  @click="addOrRemove(scope, false)"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
        <div class="user-add-footer">
          <el-button type="primary" @click="submit"></el-button>
          &nbsp;&nbsp;
          <el-button @click="resetClick"></el-button>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import CourseForm from "./CourseForm.vue";
import { addMoreCourse } from "@api/course";
import XLSX from "xlsx";
export default {
  mounted() {
    this.$store.dispatch("getUserTeacher");
    this.$store.state.userTeacherList.forEach(item => {
      let key = item.teacher_id;
      let val = item.user_name;
      this.tutorList[val] = key;
    });
  },
  data() {
    return {
      courseList: [{}],
      multipleSelection: [],
      activeName: "first",
      file: [],
      tutorList: {}
    };
  },
  methods: {
    // 
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    // 
    addOrRemove(scope, flag) {
      if (flag) {
        // 
        if (this.courseList.length !== 1) {
          this.courseList.splice(scope.$index, 1);
        }
      } else {
        // 
        this.courseList.push({});
      }
    },
    // 
    allDelete() {
      const res = this.courseList.filter(
        item => this.multipleSelection.indexOf(item) === -1
      );
      this.courseList = res;

      if (this.courseList.length === 0) {
        this.courseList.push({});
      }
    },
    // 
    async uploadFile(params) {
      const _file = params.file;
      const fileReader = new FileReader();
      fileReader.onload = ev => {
        try {
          const data = ev.target.result;
          const workbook = XLSX.read(data, {
            type: "binary"
          });
          for (let sheet in workbook.Sheets) {
            //
            const sheetArray = XLSX.utils.sheet_to_json(workbook.Sheets[sheet]);
            const res = sheetArray.map(item => {
              let key = item[""];
              return {
                course_name: item[""],
                course_hour: item[""],
                course_classroom: item[""],
                course_credit: item[""],
                course_year_begin: item[""].split("-")[0],
                course_year_end: item[""].split("-")[1],
                course_amount: item[""],
                course_type: item[""],
                course_assess: item[""],
                teacher_id: this.tutorList[key],
                course_info: item[""]
              };
            });
            this.courseList = res;
          }
        } catch (e) {
          this.$message.warning("");
        }
      };
      fileReader.readAsBinaryString(_file);
    },
    // 
    resetClick() {
      this.courseList = [{}];
      this.multipleSelection = [];
    },
    async submit() {
      let res = await addMoreCourse(this.courseList);
      if (res) {
        this.$message({
          type: "success",
          message: "!"
        });
        this.courseList = [{}];
      }
    }
  },
  components: {
    CourseForm
  }
};
</script>

<style lang="scss" scoped>
@import "./AddUser.scss";
</style>
