<template>
  <div>
    <div class="user-add-header cl">
      <el-upload
        :disabled="false"
        action="customize"
        name="openFile"
        :http-request="uploadFile"
        :file-list="file"
        :show-file-list="false"
        class="fr"
      >
        <el-button type="primary" class="fr" icon="el-icon-bottom-right"
          ></el-button
        >
      </el-upload>
      <a
        href="http://localhost:3000/template/%E7%94%A8%E6%88%B7%E5%AF%BC%E5%85%A5%E6%A8%A1%E6%9D%BF.xlsx"
        ><el-button type="text" class="fr"></el-button></a
      >
    </div>
    <div class="user-add-content">
      <div>
        <el-button type="text" class="fl all" @click="allDelete"
          ></el-button
        >
      </div>
      <el-table
        ref="multipleTable"
        :data="userList"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange"
        v-loading="loading"
      >
        <el-table-column type="selection" width="50"> </el-table-column>
        <el-table-column label="" width="50" type="index"></el-table-column>
        <el-table-column label="" min-width="170" align="center">
          <template slot-scope="scope"
            ><el-input v-model="scope.row.user_username"></el-input
          ></template>
        </el-table-column>
        <el-table-column label="" min-width="170" align="center">
          <template slot-scope="scope">
            <el-input v-model="scope.row.user_password"></el-input>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="170" align="center">
          <template slot-scope="scope">
            <el-select
              v-model="scope.row.role_id"
              placeholder=""
              style="width:100%"
              disabled
            >
              <el-option
                :label="role.role_name"
                :value="role.role_id"
                v-for="role in roleList"
                :key="role.role_id"
              ></el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="170" align="center">
          <template slot-scope="scope">
            <el-input v-model="scope.row.user_name"></el-input>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="170" align="center">
          <template slot-scope="scope">
            <el-input v-model="scope.row.user_last_name"></el-input>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="170" align="center">
          <template slot-scope="scope">
            <el-radio v-model="scope.row.user_sex" label=""></el-radio>
            <el-radio v-model="scope.row.user_sex" label=""></el-radio>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="100" align="center">
          <template slot-scope="scope">
            <el-input v-model.number="scope.row.user_age"></el-input>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="170" align="center">
          <template slot-scope="scope">
            <el-select
              v-model="scope.row.user_nation"
              placeholder=""
              style="width:100%"
            >
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="170" align="center">
          <template slot-scope="scope">
            <el-input v-model="scope.row.user_tel"></el-input>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="170" align="center">
          <template slot-scope="scope">
            <el-date-picker
              v-model="scope.row.user_birthday"
              type="date"
              placeholder=""
              style="width:100%"
            >
            </el-date-picker>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="170" align="center">
          <template slot-scope="scope">
            <el-input v-model="scope.row.user_address"></el-input>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="170" align="center">
          <template slot-scope="scope">
            <el-input v-model="scope.row.user_heath"></el-input>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="170" align="center">
          <template slot-scope="scope">
            <el-select
              v-model="scope.row.user_culture"
              placeholder=""
              style="width:100%"
            >
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </template>
        </el-table-column>
        <el-table-column label="" width="100" fixed="right">
          <template slot-scope="scope">
            <el-button
              type="danger"
              icon="el-icon-delete"
              size="mini"
              circle
              @click="addOrRemove(scope, true)"
              :disabled="userList.length === 1"
            ></el-button>
            <el-button
              type="primary"
              icon="el-icon-plus"
              size="mini"
              circle
              v-if="scope.$index === userList.length - 1"
              @click="addOrRemove(scope, false)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="user-add-footer">
      <el-button type="primary" @click="addMoreUser"></el-button>
      &nbsp;&nbsp;
      <el-button @click="resetClick"></el-button>
    </div>
  </div>
</template>

<script>
import XLSX from "xlsx";
import { mapState } from "vuex";
import { addMoreUser } from "@api/user";
export default {
  mounted() {
    this.$store.dispatch("getAllRoll");
  },
  data() {
    return {
      userList: [{ role_id: 0 }],
      multipleSelection: [],
      activeName: "second",
      file: [],
      loading: false
    };
  },
  methods: {
    // 
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    // 
    addOrRemove(scope, flag) {
      if (flag) {
        // 
        if (this.userList.length !== 1) {
          this.userList.splice(scope.$index, 1);
        }
      } else {
        // 
        this.userList.push({ role_id: 0 });
      }
    },
    // 
    allDelete() {
      const res = this.userList.filter(
        item => this.multipleSelection.indexOf(item) === -1
      );
      this.userList = res;

      if (this.userList.length === 0) {
        this.userList.push({ role_id: 0 });
      }
    },
    async addMoreUser() {
      const result = await addMoreUser(this.userList);
      if (result) {
        this.$message({
          type: "success",
          message: "!"
        });
        this.userList = [{ role_id: 0 }];
      }
    },
    // 
    async uploadFile(params) {
      const _file = params.file;
      const fileReader = new FileReader();
      this.loading = true;
      fileReader.onload = ev => {
        try {
          const data = ev.target.result;
          const workbook = XLSX.read(data, {
            type: "binary"
          });
          for (let sheet in workbook.Sheets) {
            //
            const sheetArray = XLSX.utils.sheet_to_json(workbook.Sheets[sheet]);
            const res = sheetArray.map(item => {
              return {
                user_username: item[""],
                user_password: item[""],
                role_id: item[Object.keys(item)[2]],
                user_name: item[""],
                user_sex: item[""],
                user_age: item[""],
                user_address: item[""],
                user_nation: item[""],
                user_tel: item[""],
                user_birthday: this.$.formatDate(item[""]),
                user_last_name: item[""],
                user_heath: item[""],
                user_culture: item[""]
              };
            });
            // this.userList.push(...res);
            this.userList = res;
          }
          this.loading = false;
        } catch (e) {
          this.$message.warning("");
        }
      };
      fileReader.readAsBinaryString(_file);
    },
    // 
    resetClick() {
      this.userList = [{ role_id: 0 }];
      this.multipleSelection = [];
    }
  },
  computed: {
    ...mapState(["roleList"])
  }
};
</script>

<style lang="scss" scoped>
@import "./AddUser.scss";
</style>
